import { SET_COUNTRY_WIDE_FILTER } from '../actions';
import initialState from './initialState';


function filtersReducer(state = initialState.filters, action) {
    switch (action.type) {
    case SET_COUNTRY_WIDE_FILTER:
        return {
            ...state,
            countryWide: action.countryWideFilter
        };
    default:
        return state;
    }
}

export default filtersReducer;