import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Translate } from "react-localize-redux";
import Paper from '@material-ui/core/Paper'
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import {
    BarChart, ResponsiveContainer, XAxis, YAxis, CartesianGrid, 
    Tooltip, Legend, Bar, LabelList
} from 'recharts';
import {HospitalisationsContainer as Container} from '../containers';
import ChartTitle from './ChartTitle';
import { formatDate, formatNumber } from '../utils';
import {HOSPITALISATION_AREA_SHORT_LABEL_TRANSLATION_KEY_MAP, HOSPITALISATION_AREA_LABEL_TRANSLATION_KEY_MAP} from '../constants';

const DEFAULT_BAR_OPACITY = 0.8;
const IN_PATIENT_CARE_MEASURE_ID = "inpatientCare";
const IN_INTENSIVE_CARE_MEASURE_ID = "inIntensiveCare";

class HospitalisationsPanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            opacity: {
                [IN_PATIENT_CARE_MEASURE_ID]: DEFAULT_BAR_OPACITY,
                [IN_INTENSIVE_CARE_MEASURE_ID]: DEFAULT_BAR_OPACITY
            },
            visibleMeasures: [
                IN_PATIENT_CARE_MEASURE_ID, 
                IN_INTENSIVE_CARE_MEASURE_ID
            ]
        };
    }

    componentDidMount() {
        if(!this.props.hospitalisations.loaded) {
            this.props.statisticsActions.fetchHospitalisations();
        }
    }

    _formatDate = (date) => { 
        return formatDate(date, this.props.currentLanguage, 'DD MMMM YYYY');
    }

    _formatXAxis = (tickItem) => { 
        const translationKey = HOSPITALISATION_AREA_SHORT_LABEL_TRANSLATION_KEY_MAP[tickItem]; 
        return this.props.translate(translationKey);
    }

    _formatYAxis = (tickItem) => { 
        return formatNumber(tickItem, this.props.currentLanguage);
    }

    _formatLegendItem = (value, entry) => {
        const translationKey = `statistics.${value}`;
        return <Translate id={translationKey} />;
    }

    _formatLooltipLabel = (value, entry) => {
        const translationKey = HOSPITALISATION_AREA_LABEL_TRANSLATION_KEY_MAP[value]; 
        return this.props.translate(translationKey);
    }

    _formatLooltipItem = (value, name, props) => {
        const translationKey = `statistics.${name}`;
        return [
            formatNumber(value, this.props.currentLanguage), 
            this.props.translate(translationKey)
        ];
    }

    _toggleMeasute = (e) => {
        const measureId = e.dataKey;
        const visibleMeasures = this.state.visibleMeasures;

        this.setState({
            visibleMeasures: visibleMeasures.includes(measureId) ?
                visibleMeasures.filter(x => x !== measureId) :
                [...visibleMeasures, measureId]
        });
    }

    _onLegendMouseEnter = (o) => {
        const { dataKey } = o;
        const { opacity } = this.state;
    
  	    this.setState({
    	    opacity: { ...opacity, [dataKey]: 1 }
        });
    }

    _onLegendMouseLeave = (o) => {
        const { dataKey } = o;
        const { opacity } = this.state;
    
  	    this.setState({
    	    opacity: { 
                ...opacity, 
                [dataKey]: DEFAULT_BAR_OPACITY 
            },
        });
    }
      
    render() {
        const { hospitalisations, className } = this.props;

        if(!hospitalisations.loaded) {
            return (
                <Paper className={className}>
                    <Translate id={"loading"} />
                </Paper>
            );
        }

        const { opacity, visibleMeasures } = this.state;
        const { hospitalisationsByArea } = hospitalisations.data;
        const day = hospitalisationsByArea.day;

        return (
            <Paper className={className}>
                <ChartTitle>
                    <Translate id="statistics.numberOfPeopleInHospitals" data={{ date: this._formatDate(day) }}/>
                    <Box pt={1}>
                        <Typography align="center" color="textSecondary" component="p">
                            <Translate id="layout.hospitalisationsAbout" 
                                data={{ 
                                    thlLink: <Link href="https://thl.fi/en/web/infectious-diseases-and-vaccinations/what-s-new/coronavirus-covid-19-latest-updates/situation-update-on-coronavirus" target="_blank" rel="noreferrer"><Translate id="layout.here" /></Link>,
                                }} 
                            />
                        </Typography>
                    </Box>
                </ChartTitle>
                <div style={{ width: '100%', height: '300px'}}>
                    <ResponsiveContainer>
                        <BarChart
                            data={hospitalisationsByArea.values}
                            margin={{
                                top: 20, right: 30, bottom: 20, left: 20,
                            }}
                        >
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="areaId" tickFormatter={this._formatXAxis}/>
                            <YAxis tickFormatter={this._formatYAxis}/>
                            <Tooltip cursor={{ fill: "#FFF1C9", fillOpacity: 0.3 }} labelFormatter={this._formatLooltipLabel} formatter={this._formatLooltipItem}/>
                            <Legend 
                                formatter={this._formatLegendItem}
                                onClick={this._toggleMeasute}
                                onMouseOver={this._onLegendMouseEnter}
                                onMouseOut={this._onLegendMouseLeave}
                            />
                            <Bar 
                                dataKey={IN_PATIENT_CARE_MEASURE_ID} 
                                fill="#EA5F89" 
                                fillOpacity={opacity[IN_PATIENT_CARE_MEASURE_ID]}
                                hide={!visibleMeasures.includes(IN_PATIENT_CARE_MEASURE_ID)}
                            >
                                <LabelList dataKey={IN_PATIENT_CARE_MEASURE_ID} position="top" />
                            </Bar>
                            <Bar 
                                dataKey={IN_INTENSIVE_CARE_MEASURE_ID} 
                                fill="#9B3192" 
                                fillOpacity={opacity[IN_INTENSIVE_CARE_MEASURE_ID]}
                                hide={!visibleMeasures.includes(IN_INTENSIVE_CARE_MEASURE_ID)}
                            >
                                <LabelList dataKey={IN_INTENSIVE_CARE_MEASURE_ID} position="top" />
                            </Bar>
                        </BarChart>
                    </ResponsiveContainer>
                </div>
            </Paper>
          );
    }
}

HospitalisationsPanel.propTypes = {
    translate: PropTypes.func,
    currentLanguage: PropTypes.string,
    className: PropTypes.string,
    hospitalisations: PropTypes.object.isRequired,
    statisticsActions: PropTypes.object
};

export default connect(Container.mapStateToProps, Container.mapDispatchToProps)(HospitalisationsPanel);
